import loginFunc from "./loginFunc";

const registerFunc = async (username, name, email, password, setLoginFunc)=>{

    let bodyObj = {
        'username': username,
        'name': name,
        'email': email,
        'password': password
    }

    let response = await fetch('http://roar-env.eba-hb5rpyxz.eu-west-2.elasticbeanstalk.com/users/create', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(bodyObj)
    })

    if (response.status === 201) {
        console.log('Successfully created');
        await loginFunc(username, password, setLoginFunc);
    } else {
        console.log('Unsuccessful!')
    }
}

export default registerFunc;